.wf {
  font-family: sans-serif;
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  color: #495060;
  background-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.wf .ivu-load-loop {
  animation: ani-load-loop 1s linear infinite;
}
@keyframes ani-load-loop {
  from {
    transform: rotate(0);
  }
  50% {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.wf .input-group-error-append,
.wf .input-group-error-prepend {
  background-color: #fff;
  border: 1px solid #ed3f14;
}
.wf .input-group-error-append .ivu-select-selection,
.wf .input-group-error-prepend .ivu-select-selection {
  background-color: inherit;
  border: 1px solid transparent;
}
.wf .input-group-error-prepend {
  border-right: 0;
}
.wf .input-group-error-append {
  border-left: 0;
}
.wf .ivu-breadcrumb {
  color: #999;
  font-size: 14px;
}
.wf .ivu-breadcrumb a {
  color: #495060;
  transition: color 0.2s ease-in-out;
}
.wf .ivu-breadcrumb a:hover {
  color: #57a3f3;
}
.wf .ivu-breadcrumb > span:last-child {
  font-weight: 700;
  color: #495060;
}
.wf .ivu-breadcrumb > span:last-child .ivu-breadcrumb-item-separator {
  display: none;
}
.wf .ivu-breadcrumb-item-separator {
  margin: 0 8px;
  color: #dddee1;
}
.wf .ivu-breadcrumb-item-link > .ivu-icon + span {
  margin-left: 4px;
}
.wf article,
.wf aside,
.wf footer,
.wf header,
.wf nav,
.wf section {
  display: block;
}
.wf h1 {
  font-size: 2em;
