}
.wf summary {
  display: list-item;
}
.wf canvas {
  display: inline-block;
}
.wf template {
  display: none;
}
.wf [hidden] {
  display: none;
}
.wf * {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
.wf :after,
.wf :before {
