<template> 
  <Tabs :value="initTab">
    <TabPane :label="$i18next.t('AuthForm.btn.sign_up')" name="sign_up" :disabled="loadingSignIn">
      <div class="form-warp">
        <Form ref="signUpForm" :model="signUpForm" :rules="rule" label-position="top">
          <FormItem :label="$i18next.t('AuthForm.label.email')" prop="email">
            <Input type="text" v-model="signUpForm.email" :placeholder="$i18next.t('AuthForm.placeholder.email')">
            </Input>
          </FormItem>
          <FormItem :label="$i18next.t('AuthForm.label.password')" prop="password">
            <Input type="password" v-model="signUpForm.password" :placeholder="$i18next.t('AuthForm.placeholder.password')">
            </Input>
          </FormItem>
          <FormItem :label="$i18next.t('AuthForm.label.confirm_pwd')" prop="passwordCheck">
            <Input type="password" v-model="signUpForm.passwordCheck" :placeholder="$i18next.t('AuthForm.placeholder.confirm_pwd')">
            </Input>
          </FormItem>
          <div class="form-itme-button">
            <Button
            type="primary" 
            @click="handleSignUp('signUpForm')" 
            :disabled="loadingSignUp"
            :loading="loadingSignUp">
              {{ $i18next.t('AuthForm.btn.sign_up') }} 
            </Button>
            <Button 
            type="text" 
            @click="closeModel()" 
            :disabled="loadingSignUp">
              {{ $i18next.t('AuthForm.btn.cancel') }}
            </Button>
          </div>
        </Form>
      </div>
    </TabPane>

    <TabPane :label="$i18next.t('AuthForm.btn.sign_in')" name="sign_in" :disabled="loadingSignUp">
      <div class="form-warp">
        <Form ref="signInForm" :model="signInForm" :rules="rule" label-position="top">
          <FormItem :label="$i18next.t('AuthForm.label.email')" prop="email">
            <Input type="text" v-model="signInForm.email" :placeholder="$i18next.t('AuthForm.placeholder.email')">
            </Input>
          </FormItem>
          <FormItem :label="$i18next.t('AuthForm.label.password')" prop="password">
            <Input type="password" v-model="signInForm.password" :placeholder="$i18next.t('AuthForm.placeholder.password')">
            </Input>
          </FormItem>
          <div class="form-itme-button">
            <Button 
            type="primary" 
            @click="handleSignIn('signInForm')" 
            :disabled="loadingSignIn" 
            :loading="loadingSignIn">
              {{ $i18next.t('AuthForm.btn.sign_in') }}
            </Button>
            <Button 
            type="text" 
            @click="closeModel()" 
            :disabled="loadingSignIn">
              {{ $i18next.t('AuthForm.btn.cancel') }}
            </Button>
          </div>
        </Form>
      </div>
    </TabPane>
  </Tabs>
</template>

<script>
export default {
  name: 'wf-auth-form',
  props: ['initTab'],
  data () {
    const _i18next = this.$_wf.i18next
    const validatePasswordCheck = (rule, value, callback) => {
      if (value !== this.signUpForm.password) {
        callback(new Error(_i18next.t('AuthForm.error.passwords_dont_match')))
      } else {
        callback()
      }
    }
    return {
      showModal: true,
      loadingSignUp: false,
      loadingSignIn: false,
      signInForm: {
        email: '',
        password: ''
      },
      signUpForm: {
        email: '',
        password: '',
        passwordCheck: ''
      },
      rule: {
        email: [
          { required: true, message: _i18next.t('AuthForm.error.empty_email'), trigger: 'blur' },
          { type: 'email', message: _i18next.t('AuthForm.error.invalid_email'), trigger: 'blur' }
        ],
        password: [
          { required: true, message: _i18next.t('AuthForm.error.empty_pwd'), trigger: 'blur' },
          { type: 'string', min: 6, message: _i18next.t('AuthForm.error.password_min_length'), trigger: 'blur' }
        ],
        passwordCheck: [
          { required: true, message: _i18next.t('AuthForm.error.empty_confirm_pwd'), trigger: 'blur' },
          { validator: validatePasswordCheck, trigger: 'blur' }
        ]
      },
      shouldShowPassword: true
    }
  },
  computed: {
    $auth () {
      return this.$_wf.auth
    },
    $config () {
      return this.$_wf.config
    },
    $db () {
      return this.$_wf.db
    },
    $i18next () {
      return this.$_wf.i18next
    }
  },
  methods: {
    handleSignIn (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.loadingSignIn = true
          const email = this.signInForm.email
          const password = this.signInForm.password
          this.$auth.signInWithEmailAndPassword(email, password)
          .then((user) => {
            this.$db.ref(`users/${user.uid}`).once('value').then((snapshot) => {
              this.loadingSignIn = false
              this.closeModel()
              this.$Message.info(this.$i18next.t('AuthForm.success.signing_in'))
            })
          }).catch((error) => {
            this.loadingSignIn = false
            this.$Message.error(this.$i18next.t('AuthForm.error.signing_in'))
            console.log(error.code, error.message)
          })
        } else {
          this.$Message.error(this.$i18next.t('AuthForm.error.invalid_form'))
        }
      })
    },
    handleSignUp (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.loadingSignUp = true
          const email = this.signUpForm.email
          const password = this.signUpForm.password
          const displayName = email.split('@')[0]
          const photoURL = this.$config.defaultAvatarURL

          this.$auth.createUserWithEmailAndPassword(email, password)
          .then((user) => {
            let updates = {}
            updates['/displayName'] = displayName
            updates['/email'] = email
            updates['/photoURL'] = photoURL
            updates['/regDate'] = (new Date()).toISOString()

            this.$db.ref(`/users/${user.uid}`).update(updates)
            .then(() => {
              this.loadingSignUp = false
              this.closeModel()
              this.$Message.info(this.$i18next.t('AuthForm.success.signing_up'))
            }).catch((error) => {
              this.loadingSignUp = false
              console.log(error.code, error.message)
              this.$Message.error(this.$i18next.t('AuthForm.error.unknown'))
            })
          }).catch((error) => {
            this.loadingSignUp = false
            var errorCode = error.code
            var errorMessage = error.message
            switch (errorCode) {
              case 'auth/email-already-in-use':
                errorMessage = this.$i18next.t('AuthForm.error.email_already_in_use')
                break
              case 'auth/operation-not-allowed':
                errorMessage = this.$i18next.t('AuthForm.error.operation_not_allowed')
                break
              case 'auth/weak-password':
                errorMessage = this.$i18next.t('AuthForm.error.weak_password')
                break
              default:
                console.log(errorCode, error.message)
                errorMessage = this.$i18next.t('AuthForm.error.unknown')
                break
            }
            this.$Message.error(errorMessage)
          })
        } else {
          this.$Message.error(this.$i18next.t('AuthForm.error.invalid_form'))
        }
      })
    },
    closeModel () {
      this.$refs['signInForm'].resetFields()
      this.$refs['signUpForm'].resetFields()
      this.$parent.close()
    }
  }
}
</script>

<style scoped>
.form-warp { display: flex; flex-direction: column; height: 100%; padding: 10px 20px 20px 20px; justify-content: center; }
.form-itme-button button { margin: 0 10px; }
</style>



